import React from 'react'
import PropTypes from 'prop-types'
import {injectGlobal, ThemeProvider} from 'styled-components'
import theme from './themes/default'
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

injectGlobal `
html {
  height: 100%;
  box-sizing: border-box;
}
body {
  position: relative;
  margin: 0;
  min-height: calc(100% - 65px);
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
*{
  font-family: ${theme.fonts.primary} !important;
}
.modal-header-style{
  color:white !important;
  background:  ${theme.palette.primary};
}
.align-center{
  text-align: center;
}
.align-justify{
  text-align: justify;
}
.align-left{
  text-align: left;
}
.align-right{
  text-align: right;
}
`

const App = ({children}) => {

  window.scrollTo(0, 0);

  let wallpaper = '';

  switch (children.props.route.path) {
    case '/login':
      wallpaper = `23890_orig.jpg`;
      break;
    case '/static':
      wallpaper = `82168_orig.jpg`;
      break;
    case '/file':
      if (children.props.location.pathname.indexOf('file/summary') > -1) {
        wallpaper = `89714_orig.jpg`;
      }
      break;
    default:
  }

  wallpaper = wallpaper
    ? `
    background-image: url(/background/${wallpaper});
    background-size: cover;
    background-position: center center;
    background-attachment:fixed;
    `
    : `background: ${theme.palette.grayscale[1]};`;

  injectGlobal `
  body{
    ${wallpaper}
  }
  `
  return (
    <MuiThemeProvider theme={theme}>
      {children}
    </MuiThemeProvider>
  )
}

App.propTypes = {
  children: PropTypes.any
}

export default App
